export default {
// 此处编写的就是 vue组件实例的配置项   通过一定的语法 可以混入到组件内部
// data methods  computed 生命周期函数等
// 注意点：如果此处和组件内，提供了同名的data或methods，则组件内部的优先级更高
// 如果编写了我们的生命周期函数，则mixins中的生命周期函数和我们页面的生命周期函数，会用数组管理统一执行
  data () {
    return {
      title: '标题'
    }
  },
  methods: {
    sayHi () {
      console.log('hello')
    },
    // 根据登录状态，判断是否需要显示登录确认框
    // 1. 如果未登录 =》显示确认框  返回 true
    // 2. 如果已登录 -》啥也不干  返回false
    loginConfirm () {
      // 判断token是否存在 ，如果token不存在，弹确认框，如果token存在，继续请求操作
      if (!this.$store.getters.token) {
        // 弹确认框
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作',
          confirmButtonText: '去登陆',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 如果希望，跳转到登录 =>  登录后能跳转回来，需要在跳转去携带参数  （当前的路径地址）
            // this.$route.fullPath(会包含我们的参数的)
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
        return true
      }
      return false
    }
  }
}
